import { Component, OnInit } from '@angular/core';
import { ChartsModule } from 'ng2-charts/ng2-charts';
declare const AmCharts: any;

import '../../../../assets/charts/amchart/amcharts.js';
import '../../../../assets/charts/amchart/amcharts.js';
import '../../../../assets/charts/amchart/gauge.js';
import '../../../../assets/charts/amchart/pie.js';
import '../../../../assets/charts/amchart/serial.js';
import '../../../../assets/charts/amchart/light.js';
import '../../../../assets/charts/amchart/ammap.js';
import '../../../../assets/charts/amchart/worldLow.js';
import '../../../../assets/charts/amchart/continentsLow.js';
import {DashboardService} from '../../services/dashboardService';
import { Http } from '@angular/http';
import { DashboadData } from '../../models/dashboard.Model.js';


import { AmChartsService, AmChart } from "@amcharts/amcharts3-angular";

@Component({
  selector: 'app-ecommerce',
  templateUrl: './ecommerce.component.html',
  styleUrls: [
    './ecommerce.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [DashboardService,AmChartsService]
})
export class EcommerceComponent implements OnInit {

  public DashboadData:DashboadData;
  public gotData:boolean  = false;
  public top5Chemists = [];
  private chart: AmChart;
  public OrderStatuses = {
    pending: 100,
    confirmed: 200,
    ontheway: 300,
    notshipped: 400,
    shipped: 500,
    tobedelivered: 600,
    canceled: 700,
    completed: 800,
    returned: 900,
    delivered: 1000
  }
  options: any = {
    position: ['bottom', 'right'],
  };

  constructor(public _dashboardService: DashboardService,private AmCharts: AmChartsService) {

      this.DashboadData = new DashboadData();
  }





  ngOnInit() {
    this.checkOut();
    setTimeout(() => {

    


    });
  }


  public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels:string[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = true;
 
  public barChartData:any[] = [
    {data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A'},
    {data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B'}
  ];
 
  // events
  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  }
 
  public randomize():void {
    // Only Change 3 values
    let data = [
      Math.round(Math.random() * 100),
      59,
      80,
      (Math.random() * 100),
      56,
      (Math.random() * 100),
      40];
    let clone = JSON.parse(JSON.stringify(this.barChartData));
    clone[0].data = data;
    this.barChartData = clone;
    /**
     * (My guess), for Angular to recognize the change in the dataset
     * it has to change the dataset variable directly,
     * so one way around it, is to clone the data, change it and then
     * assign it;
     */
  }

  

  checkOut(){
    
    this.DashboadData = new DashboadData();
    this._dashboardService.getDashboardData().subscribe(response => {
      console.log(response.data);
      this.DashboadData = response.data;
      this.gotData = true;
      for (var i = 0; i < this.DashboadData.LatestOrdersList.length; i++) {

        var myDate = new Date(parseFloat(this.DashboadData.LatestOrdersList[i].ReceivedTime) * 1000);
        this.DashboadData.LatestOrdersList[i].ReceivedTime = myDate.toLocaleString();


        for (var prop in this.OrderStatuses) {

          if (this.DashboadData.LatestOrdersList[i].OrderStatus == this.OrderStatuses[prop]) {

            this.DashboadData.LatestOrdersList[i].OrderStatus = prop;

          }
        }

      }

    });

}





ngAfterViewInit() {
  setTimeout(() => {
 

    this.options  = {
      position : ['bottom', 'right'],
      maxStack: 8,
      timeOut: 5000,
      showProgressBar: true,
      pauseOnHover: true,
      lastOnBottom: true,
      clickToClose: true,
      preventDuplicates: false,
      preventLastDuplicates: false,
      theClass: 'bg-c-pink no-icon',
      rtl: false,
      animate: 'rotate'
    };

    AmCharts.makeChart( 'statistics_chart', {
      'type': 'serial',
      'theme': 'light',
      // 'hideCredits': true,
      'dataDateFormat': 'YYYY-MM-DD',
      'precision': 2,
      'valueAxes': [{
        'id': 'v1',
        'title': 'Sales',
        'position': 'left',
        'autoGridCount': false,
        'labelFunction': function(value) {
          return '$' + Math.round(value) + 'M';
        }
      }, {
        'id': 'v2',
        'gridAlpha': 0.1,
        'autoGridCount': false
      }],
      'graphs': [{
        'id': 'g1',
        'valueAxis': 'v2',
        'lineThickness': 0,
        'fillAlphas': 0.2,
        'lineColor': '#4099ff',
        'type': 'line',
        'title': 'Laptop',
        'useLineColorForBulletBorder': true,
        'valueField': 'market1',
        'balloonText': '[[title]]<br /><b style="font-size: 130%">[[value]]</b>'
      }, {
        'id': 'g2',
        'valueAxis': 'v2',
        'fillAlphas': 0.6,
        'lineThickness': 0,
        'lineColor': '#4099ff',
        'type': 'line',
        'title': 'TV',
        'useLineColorForBulletBorder': true,
        'valueField': 'market2',
        'balloonText': '[[title]]<br /><b style="font-size: 130%">[[value]]</b>'
      }],
      'chartCursor': {
        'pan': true,
        'valueLineEnabled': true,
        'valueLineBalloonEnabled': true,
        'cursorAlpha': 0,
        'valueLineAlpha': 0.2
      },
      'categoryField': 'date',
      'categoryAxis': {
        'parseDates': true,
        'gridAlpha' : 0,
        'minorGridEnabled': true
      },
      'legend': {
        'position': 'top',
      },
      'balloon': {
        'borderThickness': 1,
        'shadowAlpha': 0
      },
      'export': {
        'enabled': true
      },
      'dataProvider': [{
        'date': '2013-01-01',
        'market1': 0,
        'market2': 0,
        'sales1': 0
      }, {
        'date': '2013-02-01',
        'market1': 130,
        'market2': 110,
        'sales1': 0
      }, {
        'date': '2013-03-01',
        'market1': 80,
        'market2': 60,
        'sales1': 0
      }, {
        'date': '2013-04-01',
        'market1': 70,
        'market2': 200,
        'sales1': 0
      }, {
        'date': '2013-05-01',
        'market1': 180,
        'market2': 150,
        'sales1': 0
      }, {
        'date': '2013-06-01',
        'market1': 105,
        'market2': 90,
        'sales1': 0
      }, {
        'date': '2013-07-01',
        'market1': 250,
        'market2': 150,
        'sales1': 0
      }]
    });

   
    
 
  }, 75);
}


}



